<template>
  <el-dialog
    @close="reset"
    :title="formData.id > 0 ? '编辑' : '新增'"
    :visible.sync="info.isShow"
  >
    <el-form label-width="80px" :model="formData" :rules="rules" ref="formRef">
      <el-form-item label="角色名称" prop="rolename">
        <el-input placeholder="角色名称" v-model="formData.rolename"></el-input>
      </el-form-item>
      <el-form-item label="角色权限" prop="menus">
        <el-tree
          ref="treeRef"
          :data="menuList"
          show-checkbox
          node-key="id"
          default-expand-all
          :props="defaultProps"
        >
        </el-tree>
      </el-form-item>
      <el-form-item label="角色状态" prop="status">
        <el-radio-group v-model="formData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="reset">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
// 导入事件中心
import vm from "../../utils/vm";
import { mapState, mapActions } from "vuex";
export default {
  props: ["info"],
  created() {
    //初始化menulist
    this.getMenuListAction();
    vm.$on("sendListData", (obj) => {
      //更新this.fromDate
      this.formData = obj;
      //显示对话框组件
      this.info.isShow = true;
      //勾选当前角色操作权限
      setTimeout(() => {
        this.$refs.treeRef.setCheckedKeys(this.formData.menus.split(","));
      }, 100);
    });
  },
  computed: {
    ...mapState({
      menuList: (state) => state.menu.menuList,
    }),
  },
  data() {
    //自定义表单校验规则
    var validMenus = (rule, value, callback) => {
      const keys = this.$refs.treeRef.getCheckedKeys();
      if (keys.length <= 0) {
        return callback(new Error("请选择操作权限"));
      }
      //  表示表单校验通过
      callback();
    };
    return {
      isShow: true,
      //属性控件相关值
      defaultProps: {
        children: "children",
        label: "title",
      },
      formData: {
        id: 0,
        rolename: "",
        status: "",
        menus: "",
      },
      //表单校验
      rules: {
        rolename: {
          required: true,
          message: "请输入角色名称",
          trigger: "blur",
        },
        status: {
          required: true,
          message: "请选择状态",
          trigger: "blur",
        },
        menus: {
          validator: validMenus,
          trigger: "blur",
        },
      },
    };
  },
  methods: {
    ...mapActions("menu", ["getMenuListAction"]),
    //表单提交
    submit() {
      //终极表单校验
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          //1. 获取用户勾选的节点
          const keys = this.$refs.treeRef.getCheckedKeys().join(",");
          //2. 手动更新formDate.menus
          this.formData.menus = keys;
          //3. 提交数据
          const url = this.formData.id > 0 ? "/roleedit" : "/roleadd";
          this.$http.post(url, this.formData).then((res) => {
            if (res.code === 200) {
              this.$success(res.msg);
              //自定义事件通知父组件更新数据列表
              this.$emit("updateList");
              //重置数据
              this.reset();
              return false;
            }
            this.$error(res.msg);
          });
        }
      });
    },
    //数据重置
    reset() {
      // 重置表单
      this.formData = {
        rolename: "", // 角色名称
        status: "", // 角色状态
        menus: "", // 角色权限
      };
      //清空树形组件的勾选
      this.$refs.treeRef.setCheckedKeys([]);
      //关闭对话框
      this.info.isShow = false;
    },
  },
};
</script>

<style>
</style>


